import React, { PureComponent } from "react";
import ReactHtmlParser from 'react-html-parser';
import Rating from "./PopUpRating";
import { STRING_TO_URL_CONVERT } from '../../../../utils'
import ContentWithOutSlack from '../../../Themes/Elements/PostContent/PostContentConvertion'
import { isTagShop } from "../../../../actions/api";

export default class PopUpContent extends PureComponent {

  render() {
    const { contentTitle, item, Personalization, ThemeRule, allSize, isProduct, socialAction } = this.props;
    const constentHeight = !isProduct && !isTagShop && allSize.winWidth > 991 ? allSize.height - (socialAction ? 90 : 0) : null
    let contentData = item.network.id == 20 ? this.props.contentData : STRING_TO_URL_CONVERT(this.props.contentData);
    if (item.network.id == 12) contentData = ReactHtmlParser(contentData); // for RRS but we have to change from backend.

    const contentDecorateClass = Personalization.textDecorate && String(this.props.contentData).length > 0 ? ' tb_post_modal_text_decorate' : ''

    if (Personalization.widgetTheme === 47) {
      document.querySelector('html').style.overflow = "auto";
    }

    return <div className="tb_post_modal_post_wrapper" key={`content_${item.id}`}
      style={{ maxHeight: item.type != 1 ? constentHeight : '100%' }}
    >
      <div className="tb_post_modal_post_wrap_in">
        <div className={`tb_post_modal_post_content`}>
          {item.rating != null && item.rating ? <Rating rating={item.rating} network={item.network} /> : null}
          <div className={`tb_post_modal_content tb-cTBfont-${ThemeRule.font_varient} ${contentDecorateClass}`}>
            {contentTitle ? <div className="tb_post_bold_txt">{ReactHtmlParser(contentTitle)}</div> : null}

            <ContentWithOutSlack data={item} content={contentData} Personalization={Personalization} />
          </div>
        </div>
      </div>
    </div>
  }
}
