import React, { Component } from 'react';
import { themePostTracking } from '../../../../actions/themeActions';
import { fetchShopifyProduct } from '../../../../actions/cartActions';
import '../scss/PopUpProducts.scss'
import ProductImage from './ProductImage';
import ReactHtmlParser from 'react-html-parser';
class PopUpProducts extends Component {
       state = { prdImgLoaded: false, noImg: false, itemDetails: {} }
       componentDidMount() {
              const { item, wall } = this.props
              if (item && item.ugc_products && item.ugc_products.length) {
                     item.ugc_products.map(i => {
                            let { itemDetails } = this.state
                            const fetchUrl = i.UgcProduct.product_url
                            if (wall?.ProductSetting?.Button?.pdp_target == 1) {
                                   itemDetails[i.id] = { data: null, showCart: false }
                                   this.setState({ itemDetails })
                            }
                            else {
                                   if (fetchUrl && !String(fetchUrl).includes(".html")) {
                                          fetchShopifyProduct(i.UgcProduct.product_url).then(res => {
                                                 if (res.data.id) {
                                                        itemDetails[i.id] = { data: res.data, showCart: wall?.Wall?.owner == 140717 ? false : true }
                                                        this.setState({ itemDetails })
                                                 }
                                                 else {
                                                        itemDetails[i.id] = { data: null, showCart: false }
                                                        this.setState({ itemDetails })
                                                 }
                                          }).catch(error => {
                                                 itemDetails[i.id] = { data: null, showCart: false }
                                                 this.setState({ itemDetails })
                                          })
                                   }
                            }
                     })

              }

       }

       openUrl = (url) => {
              const { wall } = this.props;
              const Button = wall.ProductSetting.Button;
              var newUrl = url;
              if (parseInt(Button.newTab) == 2) window.parent.location.href = newUrl
              else window.open(newUrl, '_blank');
       }
       tracking = (wall, product) => {
              const { item } = this.props
              themePostTracking({ type: 1, action: 2, wall: wall?.Wall?.id, product_id: product.id, post: item.referenceId, owner: wall?.Wall?.owner, feed: item.feedId, currency: product.price_currency, price: product.product_discount > 0 ? product.product_discount : product.product_price })
       }

       render() {
              const { item, ugc_products, wall, openCart } = this.props;
              const { itemDetails } = this.state
              const ProductSetting = wall.ProductSetting
              const CatalogueTitle = ProductSetting.CatalogueTitle;
              const Price = ProductSetting.Price;
              const ProductTitle = ProductSetting.ProductTitle;
              const Button = ProductSetting.Button;
              return <div className="tb_pro__container">
                     <div className={`tb_pro__heading tb-cTBfont-${CatalogueTitle.varient}`}>{ReactHtmlParser(CatalogueTitle.text)}</div>
                     <div className="tb_pro__wrap">
                            {ugc_products.map((product, index) => <div className="tb_pro__item" key={index}>
                                   <div className={`tb_pro__wrap_in`}>
                                          <div className='tb_pro__img_wrapper'>
                                                 <ProductImage ImageClass="tb_pro__image" media={product.UgcProduct.product_image} size={false} itemDetails={itemDetails[product.id] ? itemDetails[product.id] : null} />
                                          </div>
                                          {(product.UgcProduct.product_title) ?
                                                 <div className={`tb_pro__title tb-cTBfont-${ProductTitle.varient}`}>{ReactHtmlParser(product.UgcProduct.product_title)}</div> : null}

                                          {product.UgcProduct.product_price > 0 || product.UgcProduct.product_discount > 0 ?
                                                 <div className="tb_pro__price_wrap">
                                                        <div className={`tb_pro__price tb-cTBfont-${Price.varient}`}>
                                                               {product.UgcProduct.price_currency_symbol}{product.UgcProduct.product_discount > 0 ? product.UgcProduct.product_discount : product.UgcProduct.product_price}
                                                        </div>
                                                        {product.UgcProduct.product_discount > 0 && product.UgcProduct.product_discount != product.UgcProduct.product_price ? <div className={`tb_pro__discount_price tb-cTBfont-${Price.varient}`}>{product.UgcProduct.price_currency_symbol}{product.UgcProduct.product_price}</div> : null}
                                                 </div>
                                                 : null}
                                          {itemDetails && Object.keys(itemDetails).length > 0 && itemDetails[product.id] && itemDetails[product.id].showCart ?
                                                 <div className={`tb_pro__link`}>
                                                        <div onClick={itemDetails[product.id]?.data?.available ? () => openCart(product.id, product, itemDetails[product.id], item) : null} className={`tb_pro__btn ${itemDetails[product.id]?.data?.available ? `` : `tb_pro_sold`} tb-cTBfont-${Button.varient}`} >{itemDetails[product.id]?.data?.available ? Button.text : 'Sold Out'}</div>
                                                 </div>
                                                 :
                                                 <div className={`tb_pro__link`}>
                                                        <div onClick={() => { this.openUrl(product.UgcProduct.product_url); this.tracking(wall, product.UgcProduct) }} className={`tb_pro__btn tb-cTBfont-${Button.varient}`} target={wall && wall.User && wall.User.id != `102810` ? "_blank" : ''}>{Button.text}</div>
                                                 </div>}
                                   </div>
                            </div>
                            )}</div>
              </div>
       }
}


export default PopUpProducts
